.homeContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    /* min-height:100vh */
  }
  
  .dropdownContainer {
    margin-bottom: 20px;
  }
  
  .dropdown {
    padding: 10px;
    font-size: 16px;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  
  .tableContainer {
    margin-bottom: 20px;
  }
  
  .table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .table th,
  .table td {
    border: 1px solid #ccc;
    padding: 10px;
    text-align: left;
  }
  
  .table th {
    background-color: #f4f4f4;
  }
  
  .buttonContainer {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
  }
  
  .button {
    padding: 10px 20px;
    font-size: 16px;
    color: white;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .button:hover {
    background-color: #0056b3;
  }
  
  .pokemonField {
    width: 1000px; /* Updated width */
    height: 600px; /* Updated height */
    border: 1px solid #ccc;
    position: relative;
    margin-top: 20px;
  }
  
  .pokemon {
    position: absolute;
    width: 20px;
    height: 20px;
  }
  